import { types } from "../actionTypes/actionTypes";
import { apiToken, SERVER_URL, _get } from "../../utils/constants";
import axios from 'axios';

export const getChampionsList = () => {
  return (dispatch) => {
    dispatch({ type: types.LIST_REQUEST });
    _get(
      SERVER_URL + "page[number]=1&page[size]=100" + apiToken,
      {},
      {},
      (res) => {
        dispatch({
          type: types.LIST_SUCCESS,
          championsList: res,
        });
      },
      (err) => {
        console.log(err);
        dispatch({ type: types.LIST_FAILED, error: err.message });
      }
    );
  };
};

export const searchChampions = (query) => {
  return (dispatch) => {
    dispatch({ type: types.SEARCH_REQUEST });
    _get(
      SERVER_URL + "search[name]=" + query + "&token=" + apiToken,
      {},
      {},
      (res) => {
        dispatch({
          type: types.SEARCH_SUCCESS,
          searchedResults: res,
        });
      },
      (err) => {
        console.log(err);
        dispatch({ type: types.SEARCH_FAILED, error: err.message });
      }
    );
  };
};


export const getChampionsListContent = () => {
  return (dispatch) => {
    dispatch({ type: types.LIST_REQUEST });
    var body = JSON.stringify({
      "articlePerPage": "10",
      "category": "",
      "from": "",
      "keyword": "",
      "pageNo": 1,
      "publishedBy": "BRIGHTMLS",
      "to": "",
      "type": ""
    });

    var config = {
      method: 'post',
      url: 'https://contentful.dev.brightmls.com/api/articlenews',
      headers: {
        'Origin': 'dev.brightmls.com',
        'Content-Type': 'application/json',
        "Access-Control-Allow-Origin": "http://localhost:3000",
        'Access-Control-Allow-Methods':'GET,PUT,POST,DELETE,PATCH,OPTIONS',
      
      },
      withCredentials: true,
      data: body
    };

    axios(config)
      .then(function (response) {
        console.log(JSON.stringify(response.data));

        dispatch({
          type: types.LIST_SUCCESS,
          championsList: response,
        });
      })
      .catch(function (error) {
        console.log(error);
        dispatch({ type: types.LIST_FAILED, error: error.message });
      });
  };
};


